import React from "react";
import Cite from 'citation-js';
import styles from "./bibliography.module.css"

export default ({ bibtex }) => {
  const bibliography = new Cite(bibtex)
  let output = bibliography.format('bibliography', {
    format: 'html',
    template: 'chicago-author-date',
    lang: 'en-US'
  })
  return (
    <div cl className={`${styles.bibliography} ${styles.bibliographyCol2}`}
      dangerouslySetInnerHTML={{__html: output}}></div>
  )
}